<template>
  <div id="register">
    <headpage></headpage>
    <center>
      <div>
        <center>
          <span>
            <strong>用户注册</strong>
          </span>
          <form class="form-horizontal" role="form">
            <div class="form-group">
              <label class="col-sm-5 control-label">手机号：</label>
              <div class="col-sm-2">
                <input type="text" v-model="phone" class="form-control" placeholder="请输入手机号" />
              </div>
            </div>
            <div class="form-group">
              <label class="col-sm-5 control-label">密码：</label>
              <div class="col-sm-2">
                <input type="password" v-model="pwd" class="form-control" placeholder="请输入六至八位密码" />
              </div>
            </div>
            <div class="form-group">
              <label class="col-sm-5 control-label">确认密码：</label>
              <div class="col-sm-2">
                <input
                  type="password"
                  v-model="pwd1"
                  class="form-control"
                  placeholder="请再次输入您设置的密码"
                />
              </div>
            </div>
            <div class="form-group">
              <label class="col-sm-5 control-label">邀请码：</label>
              <div class="col-sm-2">
                <input
                  type="text"
                  v-model="InvitationCode"
                  class="form-control"
                  placeholder="请输入邀请码"
                />
              </div>
            </div>
            <div class="form-group" id="info">
              <span>如没有邀请码请通过此邮箱与我们取得联系：</span>
              <br />
              <span>ane.zhang@be-yes.com</span>
            </div>
            <div class="form-group" id="verification">
              <label class="col-sm-5 control-label">短信验证码：</label>
              <div class="col-sm-2">
                <input
                  type="text"
                  v-model="VerificationCode"
                  class="form-control"
                  placeholder="请输入短信验证码"
                />
              </div>
            </div>
            <div class="form-group">
              <div class="col-sm-offset-5 col-sm-2">
                <input
                  type="button"
                  id="getVerificationCode"
                  @click="getVerificationCode"
                  value="获取短信验证码"
                  class="col-sm-offset-1 col-sm-10 btn btn-primary"
                />
              </div>
            </div>
            <div class="form-group">
              <div class="col-sm-offset-5 col-sm-2">
                <input type="button" @click="register" value="用户注册" class="col-sm-offset-1 col-sm-10 btn btn-primary" />
              </div>
            </div>
          </form>
        </center>
      </div>
    </center>
    <footpage></footpage>
  </div>
</template>

<script>
import axios from "axios";
import qs from "qs";

import headpage from "@/components/headbarLogin";
import footpage from "@/components/footbar";
export default {
  data() {
    return {
      phone: "",
      pwd: "",
      pwd1: "",
      InvitationCode: "",
      VerificationCode: ""
    };
  },
  components: {
    headpage,
    footpage
  },
  methods: {
    // 获取验证码
    getVerificationCode() {
      console.log(this.phone);
      // 信息验证
      if (this.phone === "") {
        alert("手机号不能为空");
      } 
      else if(String(this.phone).length !== 11){
        alert("请输入正确的手机号")
      }else if (this.pwd === "") {
        alert("密码不能为空");
      }else if(String(this.pwd).length<6 || String(this.pwd).length>8){
        alert("请设置密码长度为6至8位")
      } else if (this.pwd1 !== this.pwd) {
        alert("两次密码不一样，请重新输入");
      } else if (this.InvitationCode === "") {
        alert("邀请码不能为空");
      } else {
        //  获取短信验证码按钮样式
        let time = 60;
        $("#getVerificationCode").attr("disabled", true);
        let timer = setInterval(function() {
          if (time == 0) {
            clearInterval(timer);
            $("#getVerificationCode").attr("disabled", false);
            $("#getVerificationCode").val("获取短信验证码");
          } else {
            $("#getVerificationCode").val(time + "秒");
            time--;
          }
        }, 1000);
        // 将手机号发送给后台
        let params = {
          phoneNumber: this.phone,
          invitecode: this.InvitationCode
        };
        axios
          .post(
            "http://localhost:8080/user/getVerification",
            qs.stringify(params)
          )
          .then(response => {
            console.log(response);
            if (response.data.code === 0) {
              //   显示输入验证码
              $("#info").toggle();
              $("#verification").toggle();
              alert("发送成功，请注意查收验证码");
            }else if(response.data.code === -2){
              alert("邀请码不存在或已被注册");
              clearInterval(timer);
            $("#getVerificationCode").attr("disabled", false);
            $("#getVerificationCode").val("获取短信验证码");
            } else {
              alert("发送失败，请检查您的手机号是否正确后重新获取验证码");
              clearInterval(timer);
            $("#getVerificationCode").attr("disabled", false);
            $("#getVerificationCode").val("获取短信验证码");
            }
          });
      }
    },
    //注册
    register() {
      let params = {
        name: this.phone,
        pwd: this.pwd,
        invitecode: parseInt(this.InvitationCode),
        code: this.VerificationCode
      }
      axios
      .post("http://localhost:8080/user/register",qs.stringify(params))
      .then(response =>{
        console.log(response)
        if(response.data.code===0){
          alert("注册成功,请登录")
          this.$router.push({ path: "./" });
        }else if(response.data.code===-1){
           alert("验证码错误，请重新输入")
        }else{
          alert("注册失败")
        }
      })
    }
  }
};
</script>

<style lang="less" scoped>
#register {
  background-image: url("../img/back.png");
  position: absolute;
  height: 100%;
  width: 100%;
}
#verification {
  display: none;
}
</style>



