<template>
  <div id="login">
    <headpage></headpage>
     <div> 
        <br />
        <br />
        <form class="form-horizontal" role="form">
            <div class="form-group">
              <label class="col-sm-5 control-label">用户名：</label>
              <div class="col-sm-2">
                <input type="text" v-model="loginForm.username" class="form-control" placeholder="请输入用户名或手机号" />
              </div>
            </div>
            <div class="form-group">
              <label class="col-sm-5 control-label">密码：</label>
              <div class="col-sm-2">
                <input type="password" v-model="loginForm.password" class="form-control" placeholder="请输入密码" />
              </div>
            </div>
            <div class="form-group">
              <div class="col-sm-offset-5 col-sm-2">
                <input type="button" @click.prevent="login" value="用户登录" class="col-sm-offset-1 col-sm-10 btn btn-primary" />
              </div>
            </div>
            <div class="form-group">
              <div class="col-sm-offset-5 col-sm-2">
                <input type="button" @click="toRegister" value="用户注册" class="col-sm-offset-1 col-sm-10 btn btn-primary" />
              </div>
            </div>
            <div class="form-group">
              <div class="col-sm-offset-5 col-sm-2">
                <input type="button" @click="toFindPwd" value="忘记密码" class="col-sm-offset-1 col-sm-10 btn btn-primary" />
              </div>
            </div>
        </form>
    </div>
    <footpage></footpage>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
import axios from "axios";
import qs from "qs";

import headpage from "@/components/headbarLogin"
import footpage from "@/components/footbar"

export default {
  name: "login",
  data() {
    return {
      loginForm: {
        username: "",
        password: ""
      }
    };
  },
  components: {
    headpage,
    footpage
  },
  methods: {
    // 登录事件
    login() {
      let _this = this;
      let params = {
        name: this.loginForm.username,
        passwd: this.loginForm.password,
        // dt:Date().toLocaleString()
      };
      if (this.loginForm.username == "" || this.loginForm.password == "") {
        alert("账号或密码不能为空");
       }
      else {
        axios.post("http://152.136.211.215:8080/user/login", qs.stringify(params))
        .then(response => {
          //console.log(response.data);
          if (response.status === 200 && response.data.code === 0) {
            alert("登录成功！");
            sessionStorage.setItem("userName", this.loginForm.username);
            this.$store.dispatch("userName", this.loginForm.username);
            this.$router.push({ path: "./index" });
            return true;
          } else {
            alert("账号或密码错误，请重新输入");
            return false;
          }
        });
      }
    },
    toRegister() {
      this.$router.push({ path: "./register" });
    },
    toFindPwd() {

    }
  },
};
</script>

<style lang="less" scoped>
#login {
  background-image: url("../img/back.png");
  position: absolute;
  height: 100%;
  width: 100%;
}
.logo {
  width: 19.2%;
  height: 5%;
  margin-top: 1%;
  margin-left: 5%;
}
.name {
  font-size: 2.5rem;
  margin-left: 1%;
  color: #191970;
}
li {
  list-style: none;
}
</style>



